import Vue from 'vue'
import Router from 'vue-router'
import promise from 'es6-promise'

promise.polyfill();
Vue.use(Router);
let components = [];
const files = require.context('./pages', true, /\/.*\.vue$/);
const filenames = files.keys();
filenames.forEach(route=>{
    let path = route.match(/\.(\S*)/)[1];
    let name = route.match(/\/(\S*)\./)[1];
    components.push(
        Object.assign({
            component:()=>import('./pages'+path)
        },{name:name},{path:'/'+(name==='index'?'':name)+(name==='detail'?'/:id':'')})
    )
})
components.push({
    name:'404',
    path:'*',
    component:()=>import('./pages/404')
})
export default new Router({
    mode:'history',
    scrollBehavior(to, from, savedPosition) {
        return {x: 0, y: 0}
    },
    routes:components
})


