<template>
  <div id="app">
    <keep-alive include="index">
    <router-view class="router"></router-view>
    </keep-alive>
    <navbar></navbar>
    <BackTop></BackTop>
  </div>
</template>

<script>
import navbar from './components/layout/navbar'
import BackTop from './components/layout/scollTop'
export default {
  name: 'App',
  components:{
    navbar,
    BackTop
  },
  mounted(){
        window.addEventListener('scroll',()=>{
            let scrollTop =  window.document.documentElement.scrollTop
            if(scrollTop>240){
                this.bus.$emit('backtop',true)
            }else{
                this.bus.$emit('backtop',false)
            }
        })
    }
}
</script>


<style lang="stylus">
body
  margin 0 
  padding 0
.demo 
  height 5px 
  width 7.50rem 
  background-color #000
.ac
  text-align center
  &.lg 
    font-size .50rem
.container
  padding .10rem
  p
    font-size .4rem
    font-weight bold
</style>



